import React from "react";
import { withStyles } from "@material-ui/core/styles";
import compose from "../../../compose";

import classNames from "classnames";
import getSharedStyles from "../../../style";

import ArrowRight from "../icons/ArrowRight";
import ArrowLeft from "../icons/ArrowLeft";
const styles = (theme) => ({
    ...getSharedStyles(theme),
    button: {
        maxWidth: "450px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        whiteSpace: "nowrap",
        userSelect: "none",
        fontSize: "14px",
        lineHeight: "130%",
        fontFamily: "'IBM Plex Mono', monospace",
        letterSpacing: "0.5px",
        padding: "0 20px",
        height: "52px",
        minHeight: "52px",
        background: theme.palette.primary.main,
        color: "#ffffff",
        fontWeight: "700",
        //borderRadius: '50px',
        border: `2px solid ${theme.palette.primary.main}`,
        textTransform: "uppercase",
        outline: "none",
        cursor: "pointer",
        transition: "all 0.25s ease",
        "&:hover": {
            background: theme.palette.primary.dark,
            borderColor: theme.palette.primary.dark,
        },
    },
    btnFullwidth: {
        width: "100%",
    },
    btnOutline: {
        border: `2px solid ${theme.palette.primary.main}!important`,
        background: "transparent!important",
        color: `${theme.palette.primary.main}!important`,
    },
    //color variants
    btnSecondary: {
        background: `${theme.palette.secondary.main}!important`,
        border: `2px solid ${theme.palette.secondary.main}!important`,
        color: "white!important",
        "&$btnOutline": {
            background: `transparent!important`,
            border: `2px solid ${theme.palette.secondary.main}!important`,
            color: `${theme.palette.secondary.main}!important`,
        },
    },
    //size
    btnLarge: {
        padding: "0 25px",
        height: "52px",
        fontSize: "14px",
    },
    btnSmall: {
        padding: "0 15px",
        height: "30px",
        minHeight: "30px",
        fontSize: "11px",
    },
    btnDisabled: {
        background: "#DDC5CA!important",
        borderColor: "#DDC5CA!important",
    },
    btnArrow: {
        justifyContent: "space-between",
    },
});

function Button(props) {
    const { classes, fullwidth, disabled, outline, size, variant, minWidth, arrow, arrowBack, type, children } = props;
    const handleClick = (e) => {
        e.stopPropagation();
        e.nativeEvent.stopImmediatePropagation();
        !disabled && props.onClick && props.onClick();
    };
    return (
        <>
            <button
                className={classNames(
                    props.className,
                    classes.button,
                    disabled && classes.btnDisabled,
                    fullwidth && classes.btnFullwidth,
                    outline && classes.btnOutline,
                    size === "lg" && classes.btnLarge,
                    size === "sm" && classes.btnSmall,
                    variant === "secondary" && classes.btnSecondary,
                    (arrow || arrowBack) && classes.btnArrow
                )}
                onClick={(e) => handleClick(e)}
                style={{ minWidth: minWidth ? minWidth + "px" : "0px", ...props.style }}
                type={props.type ? props.type : "button"}
            >
                {arrowBack && <ArrowLeft size={28} className={classNames(classes.mr4)} />}
                {children}
                {arrow && <ArrowRight size={28} className={classNames(classes.ml4)} />}
            </button>
        </>
    );
}

export default compose(withStyles(styles))(Button);
