import React from "react";
import PropTypes from "prop-types";

import { withRouter } from "react-router";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../shared/localization";

import getSharedStyles from "../../style";
import compose from "../../compose";
import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import classNames from "classnames";

import Snackbar from "@material-ui/core/Snackbar";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Grid from "@material-ui/core/Grid";

import Button from "../dobyComponents/form/Button";
import ProductSmallCard from "../dobyComponents/eshop/ProductSmallCard";
import BackpackAdd from "../dobyComponents/icons/BackpackAdd";
import Markdown from "../shared/Markdown";
import CircleLoader from "../dobyComponents/CircleLoader";
import Modal from "../dobyComponents/Modal";

import { formatPrice /*, getCurrencySymbol*/ } from "../../utils/helpers";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    root: {
        backgroundColor: "#f7f7f8",
    },
    imageContainer: {
        width: "100%",
        height: "300px",
        overflow: "hidden",
        position: "relative",
        //background: 'red',
        // margin: -2 * theme.spacing.unit
    },
    productImage: {
        position: "absolute",
        width: "100%",
        height: "100%",
        top: "0",
        left: "0",
        objectFit: "contain",
    },
    productVariantBtn: {
        padding: "8px 14px",
        minWidth: "43px",
        textAlign: "center",
        border: `2px solid ${theme.palette.secondary.main}`,
        cursor: "pointer",
    },
    productVariantBtnSelected: {
        background: theme.palette.secondary.main,
        color: "white",
    },
    productVariantBtnDisabled: {
        color: "#C8D2E1",
        borderColor: "#C8D2E1",
        cursor: "not-allowed",
    },
    "@media screen and (max-width: 330px)": {
        productSmallCardHolder: {
            maxWidth: "100%",
            flexBasis: "100%",
        },
    },
    "@media screen and (min-width: 330px) and (max-width: 380px)": {
        productSmallCardHolder: {
            "&:nth-of-type(2n+1)": {
                paddingRight: "4px",
            },
            "&:nth-of-type(2n+2)": {
                paddingLeft: "4px",
            },
        },
    },
    productModalClose: {
        position: "fixed",
        top: "15px",
        right: "15px",
        cursor: "pointer",
        zIndex: "10",
        [theme.breakpoints.up("sm")]: {
            position: "absolute",
            top: "30px",
            right: "30px",
        },
    },
});

const _defaultState = {
    detailImage: null,
    selectedVariants: {},
    selectedProduct: null,
    showSnackbar: false,
    lastLocation: window.location,
    defaultProduct: null,
    detailLoading: true,
    productDetailModalOpen: false,
    productDetailCatalogId: null,
    variants: {},
};

class ProductDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = _defaultState;
    }

    async componentDidMount() {
        this.fetchData();
    }

    componentDidUpdate() {
        if (this.state.lastLocation !== window.location.href) {
            this.setState({ ..._defaultState, lastLocation: window.location.href });
            this.fetchData();
        }
    }

    fetchDetailImage = async () => {
        if (!this.state.defaultProduct) return;

        const { imageId } = this.state.defaultProduct;
        if (imageId) {
            const data = await this.props.doby.getFile(imageId);
            let reader = new FileReader();
            reader.onloadend = () => this.setState({ detailImage: reader.result });
            reader.readAsDataURL(data);
        }
    };

    fetchData = async () => {
        const _id = this.props.inModal ? this.props.catalogId : this.props.match.params._id;
        await this.props.doby.getProductsByCatalogId(_id);

        let filter = [
            { field: "main", value: true },
            { field: "disabled", value: false },
        ];
        await this.props.doby.getProducts(filter);

        const catalogProducts = this.props.doby.catalog.data;
        if (catalogProducts[0]) {
            this.setState({ defaultProduct: catalogProducts[0] }, () => this.fetchDetailImage());
        }

        //NOTE: if we have better way of doin it let me know
        const enumTypes = this.props.store.enumTypes.data;
        const productsVariantKeys = Object.keys(this.props.doby.productVariants.data);

        const parameterEnumsForProduct = enumTypes.filter((x) => productsVariantKeys.includes(x._id.toString()));

        filter = [
            {
                field: "enumTypeId",
                op: "in",
                value: parameterEnumsForProduct.map((x) => x._id),
            },
        ];

        const getedEnum = await this.props.doby.getEnums(enumTypes, filter);

        let variants = {};
        for (let currentItem of getedEnum.data) {
            if (!variants[currentItem.enumTypeId]) variants[currentItem.enumTypeId] = [];

            let variantType = variants[currentItem.enumTypeId];

            const haveProductParameter = this.props.doby.productVariants.data[currentItem.enumTypeId.toString()].data.find(
                (x) => x._id === currentItem._id
            );
            variantType.push({
                ...currentItem,
                disabled: haveProductParameter === undefined,
            });
        }

        this.setState({ variants });

        //NOTE: otherwise we are selecting first available parameter
        var defaultSelectedVariants = {};
        for (let variantKey in this.props.doby.productVariants.data) {
            const { data } = this.props.doby.productVariants.data[variantKey];

            if (!this.state.selectedVariants[variantKey] && data.length) {
                defaultSelectedVariants[variantKey] = data[0]._id;
            }
        }

        this.setState(
            (state) => {
                return {
                    selectedVariants: {
                        ...state.selectedVariants,
                        ...defaultSelectedVariants,
                    },
                };
            },
            () => {
                const product = this.getProductByParametersSelection();
                this.setState({ selectedProduct: product, detailLoading: false });
            }
        );
    };

    handleChange = (name, value) => this.setState({ [name]: value });

    handleChangeVariant = (name, value) => {
        this.setState(
            (state) => {
                return {
                    ...state,
                    selectedVariants: {
                        ...state.selectedVariants,
                        [name]: value,
                    },
                };
            },
            () => {
                const product = this.getProductByParametersSelection();
                this.setState({ selectedProduct: product }, () => this.fetchDetailImage());
            }
        );
    };

    handleProductImageLoaded = (e) => {
        let width = e.target.naturalWidth;
        let height = e.target.naturalHeight;
        if (height < width) {
            e.target.style.height = "100%";
            e.target.style.width = "auto";
        } else {
            e.target.style.width = "100%";
            e.target.style.height = "auto";
        }
    };

    renderVariants = () => {
        const { classes } = this.props;
        const generateVariant = (variantKey) => {
            const data = this.state.variants[variantKey];
            const selected = this.state.selectedVariants[variantKey] ? this.state.selectedVariants[variantKey].toString() : "";
            return (
                <div className={classNames(classes.mb2)} key={variantKey}>
                    <div className={classNames(classes.mb2, classes.textMono, classes.textM)}>{this.props.translate(variantKey)}</div>
                    <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.flexWrap)}>
                        {data.map((item, index) => {
                            //console.log(item.disabled);

                            return (
                                <div
                                    //disabled={selected === item._id}
                                    onClick={() => !item.disabled && this.handleChangeVariant(variantKey, item._id)}
                                    className={classNames(
                                        classes.productVariantBtn,
                                        classes.mr2,
                                        classes.mb2, 
                                        classes.textMono,
                                        classes.fontSemibold,
                                        selected === item._id && classes.productVariantBtnSelected,
                                        item.disabled && classes.productVariantBtnDisabled
                                    )}
                                    key={index}
                                >
                                    {item.name}
                                </div>
                            );
                        })}
                    </div>
                </div>
            );
        };

        const keys = Object.keys(this.state.variants);
        return <>{keys.map((key) => generateVariant(key))}</>;
    };

    getProductByParametersSelection = () => {
        //NOTE: musme vybrat tolko dropdownov kolko mame unikatnych parametrov pre produkt variant
        if (Object.keys(this.state.selectedVariants).length !== Object.keys(this.props.doby.productVariants.data).length) return null;

        for (let variant of this.props.doby.catalog.data) {
            let hasAllRequirements = true;
            for (let variantKey in this.state.selectedVariants) {
                if (variant.tech_param && variant.tech_param[variantKey] !== this.state.selectedVariants[variantKey]) {
                    hasAllRequirements = false;
                    break;
                }
            }

            if (hasAllRequirements) return variant;
        }
        return null;
    };

    addToCart = () => {
        if (this.state.selectedProduct) {
            this.props.doby.cartAddProduct(this.state.selectedProduct);
            this.setState({ showSnackbar: true });
        }
    };

    showProductDetail = (product) => {
        if (product.catalog_id && product.catalog_id.length > 0) this.props.history.push(`/eshop/product/${product.catalog_id}`);
    };

    renderSimillarProducts = () => {
        const { products } = this.props.doby;
        const { classes } = this.props;

        let uniqueProducts = [];
        products.data.forEach((product) => {
            let found = false;
            uniqueProducts.forEach((item) => {
                if (product.catalog_id === item.catalog_id) {
                    found = true;
                    return;
                }
            });
            if (!found) uniqueProducts.push(product);
        });

        return uniqueProducts.map((product) => {
            return (
                <Grid item xs={6} sm={3} lg={2} key={product._id} className={classNames(classes.productSmallCardHolder, classes.mb3)}>
                    <ProductSmallCard
                        key={product._id}
                        title={product.name}
                        price={product.price_with_tax}
                        thumbnailId={product.thumbnailId}
                        catalogId={product.catalog_id}
                        onClickViewDetails={() => this.showProductDetail(product)}
                        onClickAddCart={() => this.openProductModal(product.catalog_id)}
                    />
                </Grid>
            );
        });
    };

    handleCloseProductDetailModal = () => {
        this.setState({ productDetailModalOpen: false });
    };

    openProductModal = (catalogId) => {
        this.setState({
            productDetailModalOpen: true,
            productDetailCatalogId: catalogId,
        });
    };

    renderProductDetail = () => {
        const { classes, inModal } = this.props;
        const price = this.state.selectedProduct ? formatPrice(this.state.selectedProduct.price_with_tax) : "";

        if (this.state.selectedProduct)
            console.log(this.state.selectedProduct.max_order_count - this.state.selectedProduct.order_count, this.state.selectedProduct.disabled);

        const getOutOfStockMsg = () =>
            this.state.selectedProduct && this.state.selectedProduct.unavailable_text
                ? this.state.selectedProduct.unavailable_text
                : this.props.translate("out_stock");

        const isOrderDisabled =
            !this.state.selectedProduct ||
            (this.state.selectedProduct && this.state.selectedProduct.disabled) ||
            (this.state.selectedProduct &&
                this.state.selectedProduct.max_order_count !== 0 &&
                this.state.selectedProduct.max_order_count - this.state.selectedProduct.order_count <= 0);

        return (
            <>
                {this.state.detailLoading && <CircleLoader center className={classNames(classes.py8)} />}
                {!this.state.detailLoading && (
                    <div className={classNames(classes.container, !this.props.inModal && classes.mt5)}>
                        <Grid container direction="row" justify="flex-start">
                            <Grid item sm={inModal ? 1 : 2} className={classNames(classes.dNone, classes.dSmBlock)}></Grid>

                            <Grid item xs={12} sm={inModal ? 10 : 8}>
                                <Grid container>
                                    <Grid item xs={12} md={6} xl={5}>
                                        <div className={classes.imageContainer}>
                                            {this.state.detailImage && (
                                                <img
                                                    className={classes.productImage}
                                                    src={this.state.detailImage}
                                                    //onLoad={(e) => { this.handleProductImageLoaded(e) }}
                                                    alt={""}
                                                />
                                            )}
                                        </div>
                                    </Grid>
                                    <Grid item xl={1} className={classNames(classes.dNone, classes.dXlBlock)}></Grid>
                                    <Grid item xs={12} md={6} xl={6} className={classNames()}>
                                        <h4 className={classNames(classes.mt5, classes.dMdNone)}>
                                            {this.state.selectedProduct
                                                ? this.state.selectedProduct.name
                                                : this.state.defaultProduct
                                                ? this.state.defaultProduct.name
                                                : ""}
                                        </h4>
                                        <h5 className={classNames(classes.mt5, classes.dNone, classes.dMdBlock)}>
                                            {this.state.selectedProduct
                                                ? this.state.selectedProduct.name
                                                : this.state.defaultProduct
                                                ? this.state.defaultProduct.name
                                                : ""}
                                        </h5>

                                        <div
                                            className={classNames(
                                                classes.dFlex,
                                                classes.alignItemsCenter,
                                                classes.justifyContentBetween,
                                                classes.mt4
                                            )}
                                        >
                                            <div className={classNames(classes.textMono, classes.fontSemibold)}>
                                                {price}
                                                {this.state.selectedProduct && (
                                                    <span
                                                        className={classNames(classes.textM, classes.textGray400, classes.fontRegular, classes.ml2)}
                                                    >
                                                        {this.props.translate("with_tax")}
                                                    </span>
                                                )}
                                            </div>
                                            <div className={classes.productStock}>
                                                {this.state.selectedProduct && this.state.selectedProduct.count > 0
                                                    ? this.props.translate("in_stock")
                                                    : getOutOfStockMsg()}
                                            </div>
                                        </div>

                                        <div className={classNames(classes.mt4)}>{this.renderVariants()}</div>

                                        <div className={classNames(classes.dFlex, classes.justifyContentCenter)}>
                                            <Button
                                                className={classNames(classes.mt4, classes.mb3)}
                                                onClick={this.addToCart}
                                                disabled={isOrderDisabled}
                                                fullwidth
                                            >
                                                {this.props.translate("add_to_cart")}{" "}
                                                <BackpackAdd size={30} className={classNames(classes.textWhite, classes.ml2)} />
                                            </Button>
                                        </div>

                                        <div className={classNames(classes.mt4, classes.mb8)}>
                                            <Markdown>{this.state.selectedProduct ? this.state.selectedProduct.long_description : ""}</Markdown>
                                        </div>
                                    </Grid>
                                </Grid>
                            </Grid>

                            <Grid item sm={inModal ? 1 : 2} className={classNames(classes.dNone, classes.dSmBlock)}></Grid>
                        </Grid>
                    </div>
                )}

                {!this.props.inModal && (
                    <div className={classNames(classes.bgLight)}>
                        <div className={classNames(classes.container, classes.mt8, classes.mtLg12, classes.py10)}>
                            <Grid container direction="row" justify="flex-start">
                                <Grid item xs={12} className={classNames()}>
                                    <h4 className={classNames(classes.uppercase, classes.mb5)}>{this.props.translate("find_similar_products")}</h4>
                                </Grid>
                            </Grid>
                            <Grid container spacing={16}>
                                {this.renderSimillarProducts()}
                            </Grid>
                        </div>
                    </div>
                )}

                {!this.props.inModal && (
                    <Modal
                        isOpen={this.state.productDetailModalOpen}
                        wrapperClassName={classNames(classes.p0, classes.challengeModalWrapper)}
                        onClose={this.handleCloseProductDetailModal}
                    >
                        <div className={classNames(classes.productModalClose)} onClick={this.handleCloseProductDetailModal}>
                            <CloseIcon size={32} />
                        </div>

                        <div className={classNames(classes.pt5)}>
                            <ProductDetail {...this.props} inModal catalogId={this.state.productDetailCatalogId} />
                        </div>
                    </Modal>
                )}
            </>
        );
    };

    handleClose = () => this.setState({ showSnackbar: false });
    render() {
        return (
            <>
                {this.renderProductDetail()}

                <Snackbar
                    anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "left",
                    }}
                    open={this.state.showSnackbar}
                    autoHideDuration={6000}
                    onClose={this.handleClose}
                    message={this.props.translate("successfully_added_to_cart")}
                    action={
                        <React.Fragment>
                            <IconButton size="small" aria-label="close" color="inherit" onClick={this.handleClose}>
                                <CloseIcon fontSize="small" />
                            </IconButton>
                        </React.Fragment>
                    }
                />
            </>
        );
    }
}

ProductDetail.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(withRouter, withStyles(styles), withDoby, withStore, withUser, withLocalization)(ProductDetail);
